import {
  HashRouter,
  Switch,
  Route,
  NavLink
} from "react-router-dom";
import './main.css';
import './page.css';
import React from 'react';

import About from "./pages/About";
import Contact from "./pages/Contact";
import Home from "./pages/Home";

import {Facebook, Twitter} from 'react-feather';

class Header extends React.Component {

  toggleNav () {
    this.setState({navToggled:!this.state.navToggled})
  }

  hideNav() {
    this.setState({navToggled:false});
    window.scrollTo(0,0);
  }

  updateScroll () {
    this.setState({isSticky: window.scrollY > this.state.height})
  }

  constructor(props) {
    super(props);
    this.state = {
      navToggled:false
    }
    this.toggleNav = this.toggleNav.bind(this);
    this.hideNav = this.hideNav.bind(this);
    this.updateScroll = this.updateScroll.bind(this);
  }

  componentDidMount () {
    let height = document.querySelector(".App header").clientHeight;
    this.setState({height: height});
    window.addEventListener("scroll",this.updateScroll,false);
  }

  componentWillUnmount () {
    window.removeEventListener("scroll",this.updateScroll,false);
  }
  
  render () {
    return (
    <header className = {this.state.isSticky ? 'isSticky' : ''}>
      <img src="./imgs/logo.png" id='logo' alt="Parents Across Borders"/>
      <div id="nav-toggler" onClick={this.toggleNav} className={this.state.navToggled? 'toggled' : ''}>
          <div></div>
          <div></div>
          <div></div>
        </div>
          <nav onClick={this.hideNav} className={this.state.navToggled? 'toggled' : ''}>
          <NavLink exact activeClassName="nav-active" to="/">Home</NavLink>
          <NavLink exact activeClassName="nav-active" to="/about">About</NavLink>
          <NavLink exact activeClassName="nav-active" to="/contact">Contact</NavLink>
          </nav>
          <div class="donate-wrapper">
    <button id="donate"></button>
    </div>
    </header>
    )
  }
}

class Footer extends React.Component {
  render () {
    return (
      <footer>
        <a href="tel:+31555555555">tel: +31555555555</a>
        <a href="mailto:contact@pab.org">contact@pab.org</a>
        <div id="socials">
          <a href="https://twitter.com" target="_blank" rel="noreferrer">
            <Twitter />
          </a>
          <a href="https://facebook.com" target="_blank" rel="noreferrer">
            <Facebook />
          </a>
        </div>
      </footer>
    )
  }
}


class App extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      navToggled: false,
      headerHeight:0
    }
    
  }

  

  render() {
    return (
    <HashRouter>
      <div className="App">
        <Header />
        <Switch>
          <Route exact path='/' component={Home}/>
          <Route path='/about' component={About}/>
          <Route path='/contact' component={Contact}/>
        </Switch>
        <Footer />
      </div>
    </HashRouter>
    )
  }
}


export default App;
